	module.exports = {"data":{"markdownRemark":{"html":"<h2>Center an Image Using Text Align Center</h2>\n<p>An <code>&#x3C;img></code> element has a default display value of <code>inline-block</code>. This means that it flows inline like text, but also allows the element to be given  <code>height</code> and <code>width</code> attributes.</p>\n<p>To center an image using <code>text-align: center;</code> you must place the <code>&#x3C;img></code> inside of a block-level element such as a <code>div</code>. Since the <code>text-align</code> property only applies to block-level elements, you place <code>text-align: center;</code> on the wrapping block-level element to achieve a horizontally centered <code>&#x3C;img></code>.</p>\n<h2>Documentation</h2>\n<p><a href='https://developer.mozilla.org/en-US/docs/Web/CSS/text-align' target='_blank' rel='nofollow'><strong>text-align</strong> - MDN</a></p>\n<p><a href='https://developer.mozilla.org/en-US/docs/Web/HTML/Element/img' target='_blank' rel='nofollow'><strong>&#x3C;img></strong> - MDN</a></p>","fields":{"slug":"/html/tutorials/center-an-image-using-text-align-center/"},"frontmatter":{"title":"Center an Image Using Text Align Center"}}},"pathContext":{"slug":"/html/tutorials/center-an-image-using-text-align-center/"}}

